export default {
	name: "navBar",
	props: {
		navBarData: {
			required: true,
		}
	},
	data() {
		return {

		}
	},
	methods: {
		showSliderInfo(val) {
			if (val == '/pages/classify/classify' || val == '/pages/cart/cart' || val == '/pages/member/member') {
				uni.switchTab({
					url: val
				});
				return;
			} else {
				this.$common.navigateTo(val);
				return;
			}
		}
	},
	computed: {
		navBarListStyleNum() {
			let len = this.navBarData.navBarList.length;
			let lenNum = Math.ceil(len / 2);
			if (len <= 5) {
				if (len <= 3) {
					return 3;
				} else {
					return len;
				}
			} else {
				return lenNum;
			}
		}
	}
}
